/* PT Root UI medium */
@font-face {
  font-family: "PT Root UI";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("PT Root UI Medium"),
    local("PT-Root-UI-Medium"),
    url("../fonts/PT-Root-UI_Medium.woff2") format("woff2"),
    url("../fonts/PT-Root-UI_Medium.woff") format("woff");
}

/* PT Root UI bold */
@font-face {
  font-family: "PT Root UI";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("PT Root UI Bold"),
    local("PT-Root-UI-Bold"),
    url("../fonts/PT-Root-UI_Bold.woff2") format("woff2"),
    url("../fonts/PT-Root-UI_Bold.woff") format("woff");
}
